@import "mixins";

footer.main{
  position: relative;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 30px;
  padding-top: 50px!important;
  padding-bottom: 30px!important;
  @include mob {
    display: block;
    border-radius: 15px 15px 0 0;
    overflow: hidden;
  }

  &:before{
    position: absolute;
    top: 0;
    bottom: 0;
    left: -5000px;
    display: block;
    width: 10000px;
    background: var(--color-white);
    content: "";
  }
  .item{
    position: relative;
    display: flex;
    flex-direction: column;
    @include mob {
      text-align: center;
    }
    &:nth-child(1){
      a.logo{
        display: block;
        width: 160px;
        height: 55px;
        @include mob {
          margin: 0 auto;
        }
      }
      .desc{
        padding: 30px 0 0;
        font-weight: 400;
      }
      .small{
        @include mob {
          margin-top: 20px;
        }
      }
    }
    &:nth-child(2){
      @include mob {
        display: none;
      }
      ul{
        columns: 2;
        column-gap: 40px;
        li{
          padding: 0 0 12px;
          a{
            font-size: 14px;
            line-height: 24px;
          }
        }
      }
    }
    &:nth-child(3){
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      @include mob {
        margin-top: 30px;
        align-items: center;
      }
      a{
        display: inline-block;
        &.h2{
          font-weight: 500;
          color: var(--color-text-hover);
          &:hover{
            color: var(--color-text-main);
          }
          &.email{
            margin: 8px 0 0;
          }
        }
        &.created{
          display: flex;
          align-items: flex-end;
          margin: auto 0 0;
          &:hover{
            svg{
              use{
                fill: var(--color-text-hover);
              }
            }
          }
          span{
            display: block;
            padding: 50px 0 0;
            white-space: nowrap;
          }
          svg{
            width: 121px;
            height: 14px;
            margin: 0 0 4px 3px;
            use{
              fill: var(--color-created);
            }
          }
        }
      }
      ul{
        display: grid;
        grid-template-columns: repeat(3, 48px);
        gap: 16px;
        width: auto;
        padding: 32px 0 50px;
        @include mob {
          padding: 40px 0 30px;
        }
        li{
          height: 48px;
          a{
            display: block;
            width: 100%;
            height: 100%;
            border: 1px solid var(--color-socials);
            border-radius: 10px;
            &:hover{
              border-color: var(--color-text-hover);
              svg{
                use{
                  fill: var(--color-text-hover);
                }
              }
            }
            svg{
              use{
                fill: var(--color-socials);
              }
            }
          }
        }
      }
    }
    .small{
      margin: auto 0 0;
      font-size: 12px;
      line-height: 18px;
    }
  }
}